<template>
  <Layout-box one="店铺绑定主播列表">
    <table-cmp :tableData="tableData" :tableColumn="tableColumn" @shengHeListCustom="shengHeListCustom" />
    <!-- 分页 -->
    <el-pagination class="mt-1 text-right" background :current-page="queryInit.pageNo" :page-sizes="[10, 20, 30, 40]" :page-size="queryInit.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="queryInit.total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
  </Layout-box>
</template>

<script>
import LayoutBox from '@/components/LayoutBox'
import tableCmp from '@/components/table_cmp'

export default {
  name: '',

  data () {
    return {
      tableData: [],
      tableColumn: [
        { prop: 'anchor', label: '姓名', type: 'text', formatter: 'shopZhuBoAnchorFormatter' },
        { prop: 'status', label: '状态', type: 'text', formatter: 'shopZhuBoStatusFormatter' },
        { prop: 'details', label: '理由', type: 'text' },
        { label: '操作', type: 'button', btnGroup: [
          { name: '审核', value: 'shengHeListCustom' }
        ] },
      ],
      queryInit: {
        pageNo: 1,
        pageSize: 10,
        total: 0
      }
    }
  },

  components: {
    LayoutBox,
    tableCmp,
  },

  async created () {
    await this.apiQueryAnchor()
  },

  methods: {
    async handleSizeChange (pageSize) {
      this.pageSize = pageSize
      await this.apiQueryAnchor()
    },
    async handleCurrentChange (pageNo) {
      this.pageNo = pageNo
      await this.apiQueryAnchor()
    },
    // 审核
    async shengHeListCustom (e) {
      const data = {
        id: e.scope.row.id,
        status: e.status,
        details: e.details
      }
      const res = await this.$http.post(this.$api.updateAnchorShopStatus, data)
      if (res.data.success) {
        this.$message.success('审核成功')
        await this.apiQueryAnchor()
      } else {
        this.$message.error(res.data.msg)
      }
    },

    // -----------------------------------------------------------
    // 主播注册申请列表查询
    async apiQueryAnchor () {
      const data = {
        ...this.queryInit,
      }
      const res = await this.$http.post(this.$api.findAnchorShop, data)
      this.tableData = res.data.data.list
      this.queryInit = res.data.data
    },
  }
}
</script>

<style lang="scss" scoped>

</style>
